.idBusca {
    display: flex;
    padding: 25px;
    margin: 0px 0px 0px 0px;
    justify-content: center;
    align-items: center;
    background: #1c1c1c;
}

.textBusca {
    background-color:transparent;
    font-size: 18px;
    border-radius: 40px 40px 40px 40px;
    height: 42px;
    width: 100%; 
    border: none;
    background: rgb(152, 152, 152);
    color: white;
    background-repeat: no-repeat;
    background-position: left;
    text-align: left;
    padding: 0 40px;
}

.textBusca:focus {
    outline: none;
}

.btnBusca {
    border:none;
    float:left;
    height: 42px;
    border-radius: 20px ;
    width: 490px;
    font-weight:bold;
    margin-left: 5px;
    background: greenyellow;
    text-transform: uppercase;
    cursor: pointer;
  }

.lupa {
    background: rgb(152, 152, 152);
    padding: 0;
    margin: 0;
    padding: 4px;
    border-radius: 40px 0px 0px 40px;
    padding-left: 12px;
}

@media screen and (max-width:780px) {
    .idBusca {
        flex-direction: column;
        padding: 15px 25px;
    }

    .textBusca {
        margin-bottom: 5px;
        padding: 0;
        text-align: center;
    }

    .btnBusca {
        margin-top: 5px;
        width: 100%;
        margin-left: 0;
    }

}

@media screen and (max-width:380px) {
    .textBusca::-webkit-input-placeholder {
        font-size: 15px;
    }
    .textBusca::placeholder {
        font-size: 15px;
    }

}
